.form {
    width: 320px;
  }
  
  .label {
    display: flex;
    flex-direction: column;
    gap: 5px;
    margin-bottom: 20px;
    font-weight: 700;
  }
  
  .button {
    padding: 4px 10px;
    border: 1px solid rgb(224, 218, 218);
    border-radius: 5px;
    background-color: inherit;
    cursor: pointer;
    font-weight: 700;
  }
  
  .button:active {
    background-color: rgb(0, 149, 255);
  }